.footer {
  background-color: @content-color;
}

.footer__wrapper {
  padding-top: 34px;
  padding-bottom: 23px;
}

.footer__logo {
  display: block;
  margin-left: 76px;
  margin-bottom: 31px;

  width: 128px;
  height: 24px;
  outline: none;

  &:hover {
    opacity: 0.8;
  }

  &:focus,
  &:active {
    opacity: 0.3;
  }
}

