@media (min-width: @tablet-width) {
  .footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding-top: 55px;
    padding-bottom: 60px;
  }

  .footer__logo {
    margin: 0;
  }
}
