html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  font-family: "Oswald", "Аrial", sans-serif;
  text-align: center;
  color: #444444;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
.center {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .center {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .center {
    width: 1440px;
    padding: 0 110px;
  }
}
.header .header__nav {
  display: none;
}
.header--nojs .header__toggle-nav {
  display: none;
}
.header--nojs .header__nav {
  display: block;
}
.header--opened .header__toggle-nav {
  width: 19px;
  background-image: url("../img/icon-close.svg");
}
.header--opened .header__nav {
  display: block;
}
.header--opened .header__wrapper {
  padding-bottom: 0;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 13px;
  padding-bottom: 18px;
}
.header__logo {
  display: block;
  width: 191px;
  height: 39px;
}
.header__logo:focus {
  opacity: 0.6;
}
.header__logo-img:hover {
  opacity: 0.8;
}
.header__logo-img:active {
  opacity: 0.6;
}
.header__toggle-nav {
  width: 25px;
  margin-top: 5px;
  background-color: #ffffff;
  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: none;
  cursor: pointer;
}
.header__nav {
  margin-top: 15px;
}
@media (min-width: 768px) {
  .header__wrapper {
    padding-top: 23px;
  }
  .header__toggle-nav {
    display: none;
  }
  .header .header__nav {
    display: block;
    margin-top: 0;
  }
}
@media (min-width: 1440px) {
  .header {
    position: relative;
    z-index: 1;
  }
  .header__wrapper {
    padding-top: 53px;
  }
  .header__logo {
    width: 203px;
  }
}
